@media only screen and (max-width: 768px) {

.nav label {
  display: block;
  cursor: pointer;
  text-align: right;
}

.nav {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: end;
        align-items: flex-end;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
}

.links {
  text-align: center;
  width: 100%;
  display: none;
}

.links div {
  padding-right: 0;
}

.links a {
  -ms-flex-direction: block;
      flex-direction: block;
  margin-right: 40px;

}

#toggle:checked + .links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: right;
  height: auto;
}

.welcomePage h1 {
  padding-top: 30%;
  padding-left: auto;
  padding-right: auto;
}

.icons {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.indiIcons {
  width: 60%;
}    

.pricing {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

}

@media only screen and (max-width: 576px) {
  .welcomePage h1 {
    padding-top: 50%;
    padding-left: auto;
    padding-right: auto;
  }

  .blurb {
    font-size: .85em;
  }

  .indiIcons {
    width: 80%;
  }

}



